.banner{
    
    height: 350px;
    object-fit: contain;
    
    
}

.banner_contents{
    color: white;
    margin:0 10rem;
    padding-top: 3rem;
    opacity: 10;
    
}

.banner_contents h1{
    /* text-align: center; */
    font-size: 80px;
}

.banner_btn{
    display: flex;
    justify-content: space-evenly;
}

.banner_contents p{
    font-size: 15px;
}

.banner_fade{
    height: 11.5rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,0.61),#111)
}