*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}

.container{
    margin:33px;
    padding-bottom: 2rem;
    min-height: auto;
}

.title{
    padding-bottom: 1rem;
    color: white;
}

.movieslist{
    width: 100%;
    object-fit: contain;
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
}

.movieslist::-webkit-scrollbar{
   display: none;
}

.movie_poster{
    margin-right: 20px;
    height: 150px;
    transition: 450ms;
    padding-bottom: 10px;
    
    
}

.isLarge{
    height: 260px;
}

.isLarge:hover{
    transform: scale(1.05);
}

.movie_poster:hover{
    transform: scale(1.09);
}